/**
 * 
 */
import React, {PureComponent} from "react";
import {connect} from "react-redux";
import {Button, Input, Table, Icon} from 'antd';
import exportExcel from '../../../utils';
import {Layout} from "./style";
import ModalSetting from './modalSetting';
import ModalExitSetting from './modalExitSetting';
import ModalCheck from './modalCheck';
import ModalExitCheck from './modalExitCheck';
import {getDepartmentUserList, getProgressDetail, getProgressLists,getBodyNext,getRemindUsers,getPedestalAvailable} from "../../../axios";
import moment from 'moment';
import AuthComponent from "../../common/authComponent ";
import LineWrap from '../../common/lineWrap';
import {Resizable} from 'react-resizable';

const AuthButton = AuthComponent(Button);

class SectionProgress extends PureComponent {
    state = {
        visible: false,
        exitVisible: false,
        checkVisible: false,
        exitCheckVisible: false,
        page: 1,
        size: 20,
        totalPage: 1,
        sectionList: [],
        paramsDisappear: {},
        loading: false,
        searchKey: '',
        textAreaValue: '',
        isEmpty:false,
        defaultColumns: [
            {
                title: '',
                key: 'index',
                fixed: 'left',
                width: 50,
                render: (text, record, index) => index + 1
            }, {
                title: '',
                fixed: 'left',
                width: 100,
                key: 'bridgeName',
                dataIndex: 'bridgeName',
                render: (text) => {
                    return <LineWrap title={text}/>
                }
            }, {
                title: '',
                fixed: 'left',
                width: 120,
                key:"mileage",
                dataIndex: 'mileage',
                render: (text) => {
                    return <LineWrap title={text}/>
                }
            }, {
                title: '',
                fixed: 'left',
                width: 150,
                key:"typeName",
                dataIndex: 'typeName',
                render: (text) => {
                    return <LineWrap title={text}/>
                }
            }, {
                title: '',
                fixed: 'left',
                width: 130,
                key:"name",
                dataIndex: 'name',
                render: (text) => {
                    return <LineWrap title={text}/>
                }
            },
            {
                title: '',
                fixed: 'left',
                width: 150,
                key:"pedestalName",
                dataIndex: 'pedestalName',
                render: (text) => {
                    return <LineWrap title={text}/>
                }
            },
        ],
        updateColumns: [],
        settingColumn: [{
            title: '',
            width: 100,
            align: 'center',
            render: (text, record) => {
                const beamInfo = this.props.beamInfo.toJS();
                const authCode = this.props.authCode.toJS();
                if (record.status == 5) {
                    return (<AuthButton type="link" title="" authcode='77002002' authcodes={authCode.funAuthCode} source="fun"><i
                        className="no-exit"></i></AuthButton>)
                }
                else {
                    return (<AuthButton type="link" title="" authcode='77002002' authcodes={authCode.funAuthCode} source="fun"
                                        onClick={() => this.handleSetting(record)}><i className="edit"/></AuthButton>)
                }
            }
        }],
        columns: []
    }

    components = {
        header: {
            cell: ResizeableTitle,
        },
    }

    //
    getDeptUsersList() {
        return getDepartmentUserList(this.props.beamInfo.toJS().deptId)

            .then(res => {
                return res.data.data.items
            });
    }

    //
    getBodyStatusRecord = async (logid) => {
        const {data: {data: progressDetail}} = await getProgressDetail(logid);
        this.setState({
            paramsDisappear: progressDetail
        });
    }
    // 
    hanldeBodyToNext=async (current)=> {
        //   
        const {data: {data: next}} = await getBodyNext(current.id);
        if (!next) return;
        //   
        let {data: {data: {items: userList}}} = await getRemindUsers(next.stateId)
        if (!userList.length) {
            // 
            userList = await this.getDeptUsersList();
        }
        //  
        const {data: {data: {items: pedestalList}}} = await getPedestalAvailable(next.stage,next.bodyId);
        if (!pedestalList) return;
        this.setState({
            paramsDisappear: {
                ...current,
                first: next.first,
                loading: false,
                stageName: next.stageName,
                stateName: next.stateName,
                stateId: next.stateId,
                stage: next.stage,
                beginDate: next.beginDate,
                lastBeginDate: next.lastBeginDate,
                remark: next.remark,
                status: next.status,
                userName: next.userName,
                userList: userList,
                pedestalList: pedestalList,
            }
        });
    }

    // 
    handleSetting(current) {
        this.hanldeBodyToNext(current).then(res => {
            if (this.state.paramsDisappear.status == 2) {
                this.setState({
                    exitVisible: true,
                });
            } else {
                this.setState({
                    visible: true,
                });
            }
        })
    }

    // 
    handleExitSetting(current) {
        this.hanldeBodyToNext(current);
        this.setState({
            exitVisible: true,
        });
    }

    // 
    handleCancel = () => {
        this.setState({
            visible: false,
            exitVisible: false,
            checkVisible: false,
            exitCheckVisible: false,
            textAreaValue: ''
        }, () => {
            this.getProgressList();
        });
    }

    // 
    handleCheckSetting(logid) {
        this.getBodyStatusRecord(logid);
        this.setState({
            checkVisible: true,
        });
    }

    // 
    handleCheckExitSetting(logid) {
        this.getBodyStatusRecord(logid);
        this.setState({
            exitCheckVisible: true,
        });
    }

    // 
    export(columns, sectionList) {
        const {states} = this.props.beamInfo.toJS();
        const sectionListArray=sectionList.slice();
        let sectionLists = [];
        sectionListArray.forEach((el,key) => {
            for (let i = 0; i < el.progresses.length; i++) {
                const progress = el.progresses[i];
                const curProgress = states.find(state => state.id == progress.stateId);
                if (curProgress) {
                    el[curProgress.name] = moment(progress.beginDate).format('YYYY-MM-DD')
                }
            }
            sectionLists.push({index:(key+1),...el})
        });
        const sheetList = [
            {
                headers: columns,
                data: sectionLists,
                sheetName: ''
            }
        ]
        exportExcel(sheetList, '.xlsx')
    }

    handleInputValue = (e) => {
        this.setState({
            textAreaValue: e.target.value
        })
    }

    //
    handleColumns = (stageProducedList) => {
        const {states} = this.props.beamInfo.toJS();
        const statusTitle = states.map(item => {
            return item.name
        })
        stageProducedList.forEach(el => {
            for (let i = 0; i < el.progresses.length; i++) {
                const progress = el.progresses[i];
                const curProgress = states.find(state => state.id == progress.stateId);
                if (curProgress) {
                    el[curProgress.name] = progress
                }
            }
            if(statusTitle) {
                statusTitle.map((val1,key1)=>{
                    if(el.hasOwnProperty(val1)){
                        el.isAutoWidth = true;
                    }
                })
            }
        });
        const autoColumns =  statusTitle.map((item,key) => ({
            title: item,
            dataIndex: item,
            // width:isAutoWidth?"":120,
            key:`${key}`,
            className: 'statusTitle',
            render: (text, record) => {
                // autoColumns[key].width = text?"":100;
                return (
                    record.status !== 2 ?
                        <span className = 'substr' style={{maxWidth:100,width:100}} onClick={() => this.handleCheckSetting(text.id)}
                              title={text ? moment(text.beginDate).format('YYYY-MM-DD') : ''}>
                        {text ? moment(text.beginDate).format('YYYY-MM-DD') : <>&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;</>}
                        
                            </span> :
                        <span className = 'substr' style={{maxWidth:100,width:100}} onClick={() => this.handleCheckExitSetting(text.id)}
                              title={text ? moment(text.beginDate).format('YYYY-MM-DD') : ''}>
                        {text ? moment(text.beginDate).format('YYYY-MM-DD') : <>&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;</>}
                            </span>
                )
            }
        }))
        return autoColumns;
    }

    render() {
        const {Search} = Input;
        const {sectionList, totalPage, loading, defaultColumns, updateColumns, settingColumn, textAreaValue, page, checkVisible, exitCheckVisible, size, paramsDisappear, visible, exitVisible,isEmpty, searchKey} = this.state;
        const columns = [...defaultColumns, ...updateColumns, ...settingColumn]
        const {states} = this.props.beamInfo.toJS();
        const authCode = this.props.authCode.toJS();
        const width = states.length * 100 + 800;
        const beamInfo = this.props.beamInfo.toJS();
        return (
            <Layout>
                <div className='layout-header'>
                    <AuthButton type="primary" source="fun" authcode="77002001" authcodes ={authCode.funAuthCode} onClick={() => this.export(columns, sectionList)}
                                authcode='77002001' {...beamInfo}></AuthButton>
                    <div style={{width:"200px",float:"right",position:"relative"}}>
                    <Search className="search"
                            placeholder=""
                            value ={searchKey}
                            onSearch={value => this.searchKey(value)}
                            onChange ={this.onSearchChange}
                            style={{width: 200}}
                    />
                    {isEmpty?<Icon type="close-circle" theme="filled" onClick = {this.clearSearchVal}style ={{position: "absolute",
                        right: "34px",
                        top: "11px",
                        fontSize: "13px",
                        color: "#ddd"}}/>:<></>}
                    </div>
                </div>
                <Table loading={loading}
                       components={this.components}
                       rowKey="id"
                       columns={columns}
                       dataSource={sectionList}
                       bordered={true}
                       scroll={{x: width, y: 600}}
                       className="tableStyle"
                       pagination={{
                           current: page,
                           total: totalPage,
                           pageSize: size,
                           showSizeChanger: true,
                           showQuickJumper: true,
                           pageSizeOptions: ['20', '30', '50'],
                           onChange: this.changePage,
                           showTotal: total => {
                               return `${total}`;
                           },
                           onShowSizeChange: this.sizeChange,
                       }}/>
                {/*    */}
                <ModalSetting
                    visible={visible}
                    paramsDisappear={paramsDisappear}
                    handleCancel={this.handleCancel}
                    textAreaValue={textAreaValue}
                    handleInputValue={this.handleInputValue}
                ></ModalSetting>
                {/*    */}
                <ModalExitSetting
                    exitVisible={exitVisible}
                    handleCancel={this.handleCancel}
                    paramsDisappear={paramsDisappear}
                    textAreaValue={textAreaValue}
                    handleInputValue={this.handleInputValue}
                ></ModalExitSetting>
                {/*    */}
                <ModalCheck
                    checkVisible={checkVisible}
                    paramsDisappear={paramsDisappear}
                    handleCancel={this.handleCancel}
                ></ModalCheck>
                {/*    */}
                <ModalExitCheck
                    exitCheckVisible={exitCheckVisible}
                    paramsDisappear={paramsDisappear}
                    handleCancel={this.handleCancel}
                ></ModalExitCheck>
            </Layout>
        )
    }

    // 
    async getProgressList() {
        this.setState({
            loading: true,
        });
        const {page, size, searchKey} = this.state;
        const pageParams = {
            page, size,
            name: searchKey
        }
        const {data: {totalCount:totalCount,items: responseData}} = await getProgressLists(pageParams);
        responseData.forEach(el => {
            for (let i = 0; i < el.progresses.length; i++) {
                const progress = el.progresses[i];
                el['progress' + i] = moment(progress.beginDate).format('YYYY-MM-DD')
            }
        });
        // if (!responseData || !responseData.length) return;
        this.setState({
            totalPage: totalCount,
            sectionList: responseData,
            loading: false,
            updateColumns: this.handleColumns(responseData)
        });
    }


    componentDidMount() {
        this.getProgressList();
    }

    // 
    sizeChange = (current, pageSize) => {
        // console.log(current, pageSize);
        this.setState({
            page: current,
            size: pageSize,
        }, () => {
            this.getProgressList();
        });
    }

    handleResize = index => (e, {size}) => {
        console.log(size);
        // , updateColumns, settingColumn
        // this.setState(({defaultColumns,updateColumns,settingColumn}) => {
        //     const nextColumns = [...defaultColumns, ...updateColumns, ...settingColumn];
        //     console.log(nextColumns[index],'columnscolumns');
        //     nextColumns[index] = {
        //         ...nextColumns[index],
        //         width: size.width,
        //     };
        //     console.log(nextColumns.slice(0,defaultColumns.length));
        //     return {defaultColumns: nextColumns[1],updateColumns:[],settingColumn:nextColumns[nextColumns.length-1]};
        // });
    }

    changePage = (page, size) => {
        this.setState({
            page: page,
            size: size,
        }, () => {
            this.getProgressList();
        });
    }

    //  
    searchKey(value) {
        // console.log(value);
        this.setState({
            searchKey: value,
        }, () => {
            this.getProgressList();
        });

    }
    onSearchChange = (event)=>{
        console.log(event)
        let isEmptys = event.target.value.length>0?true:false;
        this.setState({
            isEmpty:isEmptys,
            searchKey: event.target.value,
        });
    }
    clearSearchVal = () =>{
        this.setState({
            searchKey: "",
            isEmpty:true,
        }, () => {
            this.getProgressList();
        });
    }
}

const ResizeableTitle = props => {
    const {onResize, width, ...restProps} = props;

    if (!width) {
        return <th {...restProps} />;
    }

    return (
        <Resizable
            width={width}
            height={0}
            onResize={onResize}
            draggableOpts={{enableUserSelectHack: false}}
        >
            <th {...restProps} />
        </Resizable>
    );
};

const mapStateToProps = state => {
    return {
        beamInfo: state.getIn(["login", "beamInfo"]),
        authCode: state.getIn(["login", "authCode"]),
    };
};
export default connect(
    mapStateToProps,
    null
)(SectionProgress);