import React from "react";
import {Button, DatePicker, Form, Input, message, Modal, Select} from 'antd';
import moment from 'moment';
import {TextAreaSpan} from './style';
import {setBodyNext} from '../../../axios';

const {Option} = Select;
const formItemLayout = {
    labelCol: {
        xs: {span: 24},
        sm: {span: 8},
    },
    wrapperCol: {
        xs: {span: 24},
        sm: {span: 16},
    },
};

const ModalExitSetting = Form.create()(props => {
    const currentTime = moment();
    const {form, handleCancel, paramsDisappear, exitVisible,textAreaValue,handleInputValue} = props;
    const {TextArea} = Input;
    const {getFieldDecorator} = form;
    const handleSubmit = () => {
        form.validateFields((err, fieldsValue) => {
            const params = {
                stateId: paramsDisappear.stateId,
                bodyId: paramsDisappear.id,
                pedestalId:0,
                ...fieldsValue,
                beginDate: fieldsValue['beginDate'].format('YYYY-MM-DD'),
                remark:textAreaValue
            }
            setBodyNext(params).then(res => {
                if (res.code == 1) {
                    message.success('');
                    handleCancel()
                }
            })
        });
    }
    const disabledEndDate = (endValue) => {
        const defaultValue=paramsDisappear.lastBeginDate;
        const currentTime = moment();
        const startValue = currentTime;
        if (!endValue || !startValue) {
            return false;
        }
        return endValue.valueOf() < defaultValue;
    }

    return (
        <Modal
            maskClosable={false}
            title=""
            visible={exitVisible}
            onCancel={handleCancel}
            destroyOnClose
            footer={[
                //     2
                <Button key="submit" type="primary" onClick={handleSubmit}>
                    
                </Button>,
                <Button key="cancel" onClick={handleCancel}></Button>
            ]}
        >
            {/*loading={loading}*/}
            <Form {...formItemLayout} labelAlign='left' onSubmit={handleSubmit}>

                <Form.Item label="">
                    {getFieldDecorator('name', {
                        initialValue: paramsDisappear.name,
                        rules: [
                            {
                                required: true,
                            },
                        ],
                    })(<Input disabled/>)}
                </Form.Item>
                <Form.Item label="">
                    {getFieldDecorator('stateName', {
                        initialValue: paramsDisappear.stateName,
                        rules: [
                            {
                                required: true,
                            },
                        ],
                    })(<Input disabled/>)}
                </Form.Item>
                <Form.Item label="">
                    {getFieldDecorator('beginDate', {
                        initialValue: moment(paramsDisappear.beginDate ? paramsDisappear.beginDate : currentTime),
                        rules: [
                            {
                                required: true,
                                message: '',
                            },
                        ],
                    })(<DatePicker format="YYYY-MM-DD" disabledDate={disabledEndDate} style={{width: '100%'}}/>)}
                </Form.Item>
                <Form.Item label="">
                    {getFieldDecorator('userName', {
                        rules: [
                            {
                                required: true,
                                message: '',
                            },
                        ],
                    })(<Select
                        showSearch
                        style={{width: '100%'}}
                        placeholder=""
                        optionFilterProp="children"
                        filterOption={(input, option) =>
                            option.props.children.toLowerCase().indexOf(input.toLowerCase()) >= 0
                        }
                    >
                        {
                            paramsDisappear.userList && paramsDisappear.userList.length ?
                                paramsDisappear.userList.map(item => (
                                    <Option value={item.userName} key={item.realName}>{item.realName}</Option>
                                )) : null
                        }
                    </Select>)}
                </Form.Item>
                <Form.Item label="">
                    {getFieldDecorator('remark', {
                        initialValue: paramsDisappear.remark ? paramsDisappear.remark : '',

                    })(<>
                        <TextArea maxLength="100" autosize={{ minRows: 4, maxRows: 4 }} onChange={handleInputValue} value={textAreaValue}>
                        </TextArea>
                    </>)}
                    <TextAreaSpan >({textAreaValue.length}/100)</TextAreaSpan>
                </Form.Item>
            </Form>
        </Modal>
    )
});
export default ModalExitSetting;
