import React from "react";
import {DatePicker, Form, Input, Modal} from 'antd';
import moment from 'moment';

const formItemLayout = {
    labelCol: {
        xs: {span: 24},
        sm: {span: 8},
    },
    wrapperCol: {
        xs: {span: 24},
        sm: {span: 16},
    },
};

const ModalExitCheck = Form.create()(props => {
    const {form, handleCancel, paramsDisappear, exitCheckVisible} = props;
    const {TextArea} = Input;
    const {getFieldDecorator} = form;
    const currentTime = moment();

    return (
        <Modal
            maskClosable={false}
            title=""
            visible={exitCheckVisible}
            onCancel={handleCancel}
            destroyOnClose
            footer={null}
        >
            {/*loading={loading}*/}
            <Form {...formItemLayout} labelAlign='left'>

                <Form.Item label="">
                    {getFieldDecorator('name', {
                        initialValue: paramsDisappear.bodyName?paramsDisappear.bodyName:paramsDisappear.name,
                    })(<Input disabled/>)}
                </Form.Item>
                <Form.Item label="">
                    {getFieldDecorator('stateName', {
                        initialValue: paramsDisappear.stateName,
                    })(<Input disabled/>)}
                </Form.Item>
                <Form.Item label="">
                    {getFieldDecorator('beginDate', {
                        initialValue: moment(paramsDisappear.beginDate ? paramsDisappear.beginDate : currentTime),
                    })(<DatePicker format="YYYY-MM-DD" disabled style={{width: '100%'}}/>)}
                </Form.Item>
                <Form.Item label="">
                    {getFieldDecorator('userName', {
                        initialValue: paramsDisappear.userName,
                    })(<Input disabled/>)}
                </Form.Item>
                <Form.Item label="">
                    {getFieldDecorator('remark', {
                        initialValue: paramsDisappear.remark,

                    })(<TextArea rows={4} disabled>
                        <i>1111</i>
                    </TextArea>)}
                </Form.Item>
            </Form>
        </Modal>
    )
});
export default ModalExitCheck;
