import React from "react";
import {Button, DatePicker, Form, Input, message, Modal, Select} from 'antd';
import moment from 'moment';
import {TextAreaSpan} from './style';
import {setBodyNext} from '../../../axios';

const {Option} = Select;
const { confirm } = Modal;
const formItemLayout = {
    labelCol: {
        xs: {span: 24},
        sm: {span: 8},
    },
    wrapperCol: {
        xs: {span: 24},
        sm: {span: 16},
    },
};

const ModalSetting = Form.create()(props => {
    const currentTime = moment();
    const {form, handleCancel, visible, paramsDisappear, textAreaValue, handleInputValue} = props;
    const {TextArea} = Input;
    const {getFieldDecorator} = form;
    console.log(paramsDisappear,'paramsDisappear');
    const handleSubmit = () => {
        form.validateFields((err, fieldsValue) => {
            if(err) return
            const params = {
                stateId: paramsDisappear.stateId,
                bodyId: paramsDisappear.id,
                ...fieldsValue,
                beginDate: fieldsValue['beginDate'].format('YYYY-MM-DD'),
                remark:textAreaValue
            }
            if(paramsDisappear.first){
                confirm({
                    title: '',
                    okText: '',
                    cancelText: '',
                    onOk() {
                        console.log('OK');
                    },
                    onCancel() {
                        setBodyNext(params).then(res => {
                            if (res.code == 1) {
                                message.success('');
                                handleCancel()
                            }
                        })
                    },
                });
            }else{
                setBodyNext(params).then(res => {
                    if (res.code == 1) {
                        message.success('');
                        handleCancel()
                    }
                })
            }
        });
    }

    const disabledEndDate = (endValue) => {
        const defaultValue = paramsDisappear.lastBeginDate;
        const currentTime = moment();
        const startValue = currentTime;
        // console.log(currentTime,'currentTime')
        if (!endValue || !startValue) {
            return false;
        }
        // console.log(endValue.valueOf() > startValue.valueOf() )
        return  endValue.valueOf() < defaultValue;
    }
    return (
        <Modal
            maskClosable={false}
            title=""
            visible={visible}
            onCancel={handleCancel}
            destroyOnClose
            footer={[
                //     2
                <Button key="submit" type="primary" onClick={handleSubmit}>
                    
                </Button>,
                <Button key="cancel" onClick={handleCancel}></Button>
            ]}
        >
            {/*loading={loading}*/}
            <Form {...formItemLayout} labelAlign='left' onSubmit={handleSubmit}>

                <Form.Item label="">
                    {getFieldDecorator('name', {
                        initialValue: paramsDisappear.name,
                        rules: [
                            {
                                required: true,
                            },
                        ],
                    })(<Input disabled/>)}
                </Form.Item>
                <Form.Item label="">
                    {getFieldDecorator('stateName', {
                        initialValue: paramsDisappear.stateName,
                        rules: [
                            {
                                required: true,
                            },
                        ],
                    })(<Input disabled/>)}
                </Form.Item>
                <Form.Item label="">
                    {getFieldDecorator('beginDate', {
                        initialValue: moment(paramsDisappear.beginDate ? paramsDisappear.beginDate : currentTime),
                        rules: [
                            {
                                required: true,
                                message: '',
                            },
                        ],
                    })(<DatePicker format="YYYY-MM-DD" disabledDate={disabledEndDate} style={{width: '100%'}}/>)}
                </Form.Item>
                <Form.Item label="">
                    {getFieldDecorator('stageName', {
                        initialValue: paramsDisappear.stageName,
                    })(<Input disabled/>)}
                </Form.Item>
                <Form.Item label="">
                    {getFieldDecorator('pedestalId', {
                        initialValue:(paramsDisappear.pedestalList &&paramsDisappear.pedestalList.find(item=>item.k==paramsDisappear.pedestalId))?paramsDisappear.pedestalId:'',
                        rules: [
                            {
                                required: true,
                                message: '',
                            },
                        ],

                    })(<Select
                        style={{width: '100%'}}
                        placeholder=""
                    >
                        {
                            paramsDisappear.pedestalList && paramsDisappear.pedestalList.length ?
                                paramsDisappear.pedestalList.map(item => (
                                    <Option value={item.k} key={item.v}>{item.v}</Option>
                                )) : null
                        }
                    </Select>)}
                </Form.Item>
                <Form.Item label="">
                    {getFieldDecorator('userName', {
                        initialValue:paramsDisappear.userName,
                        rules: [
                            {
                                required: true,
                                message: '',
                            },
                        ],
                    })(<Select
                        style={{width: '100%'}}
                        placeholder=""
                    >
                        {
                            paramsDisappear.userList && paramsDisappear.userList.length ?
                                paramsDisappear.userList.map(item => (
                                    <Option value={item.userName} key={item.realName}>{item.realName}</Option>
                                )) : null
                        }
                    </Select>)}
                </Form.Item>
                <Form.Item label="">
                    {getFieldDecorator('remark', {
                        initialValue: paramsDisappear.remark ? paramsDisappear.remark : '',
                    })(<>
                        <TextArea maxLength="100" autosize={{minRows: 4, maxRows: 4}} onChange={handleInputValue}>
                        </TextArea>
                    </>)}
                    <TextAreaSpan>({textAreaValue.length}/100)</TextAreaSpan>
                </Form.Item>
            </Form>
        </Modal>
    )
});
export default ModalSetting;
